@import "./reset.css";
@import "./mobile-nav.css";
@import "./nav-icon.css";
@import "./media.css";

:root {
  --font-press-start: "Press Start 2P", cursive;
  --font-rubik: "Rubik", sans-serif;
  --background-color: #fdfdfd;
  --dark-background-color: #000000;
  --button-color: #0074d9;
  --button-hover-color: #005bb5;
  --nav-link-color: #000000;
  --nav-link-hover-color: #005bb5;
  --text-colour: #fdfdfdd5;
  --dark-text-colour: #000000c7;
}

body,
html {
  margin: 0;
  padding: 0;
  font-family: var(--font-rubik);
  background-color: var(--background-color);
  overflow-x: hidden;
}

section {
  max-width: 100%;
  overflow: hidden;
}

/* Intro screen */

#intro-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 98;
  background: url("../img/background/bgimage5.jpg") no-repeat center center;
  padding-top: 20px;
  padding-bottom: 20px;
  opacity: 1;
}

#intro {
  text-align: center;
  color: var(--dark-text-colour);
  font-family: var(--font-press-start);
}

.intro-logo {
  margin-bottom: 5em;
}

#logo {
  width: 600px;
  margin-bottom: 40px;
}

#countdown-intro__text {
  font-family: var(--font-press-start);
  font-size: 25px;
  color: var(--dark-text-colour);
  margin-bottom: 20px;
}

#intro__header {
  font-family: var(--font-press-start);
  font-size: 30px;
  color: var(--dark-text-colour);
  margin: 20px 0;
}

/* Intro-countdown */

#intro-countdown {
  width: 600px;
  font-family: var(--font-press-start);
  font-size: 2.5em;
  color: var(--dark-text-colour);
  margin-bottom: 40px;
  line-height: 1.5;
}

/* Get-button */

#get-button {
  background: linear-gradient(145deg, #0d88fa, #0a56a3);
  color: var(--text-colour);
  border: none;
  padding: 20px 40px;
  cursor: pointer;
  font-family: var(--font-rubik);
  font-size: 1.5em;
  transition: background-color 0.3s;
  border-radius: 18px;
  margin-top: 30px;
}

.buy-ticket-button,
#buy-ticket-button {
  background-color: var(--button-color);
  color: var(--text-colour);
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-family: var(--font-rubik);
  transition: background-color 0.3s;
}

#get-button:hover,
.buy-ticket-button:hover,
#buy-ticket-button:hover {
  background-color: var(--button-hover-color);
}

/* Intro animation */

@keyframes slide-down {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(34px);
  }
  100% {
    transform: translateY(-100%);
  }
}

#intro-screen.disappear {
  animation: slide-down 2s forwards;
}

/* Header */

#header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(145deg, #0d88fa, #0a56a3);
  color: var(--text-colour);
  transition: background-color 0.3s;
  z-index: 97;
}

#header-logo {
  width: 175px;
}

#event-location {
  font-size: 14px;
  font-family: var(--font-rubik);
  font-weight: 500;
  color: var(--text-colour);
}

.top-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 200px;
  height: 34px;
}

#header-countdown {
  font-family: var(--font-rubik);
  font-size: 14px;
  color: var(--text-colour);
  cursor: default;
}

.bottom-bar {
  padding: 0 40px;
  height: 80px;
  padding: 0 20px;
  background-color: var(--background-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#header-logo {
  width: 200px;
  margin-left: 30px;
}

#bottom-bar__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.bottom-bar a {
  margin: 0 20px;
  color: var(--nav-link-color);
  font-family: var(--font-press-start);
  font-size: 1em;
}

#header__nav {
  display: flex;
}

#header__nav a {
  position: relative;
  text-decoration: none;
  font-family: var(--font-rubik);
  color: var(--nav-link-color);
  font-size: 20px;
  font-weight: 500;
  transition: color 0.3s;
  padding: 10px 20px;
}

#header__nav a::before {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 50%;
  background-color: var(--button-color);
  transition: width 0.3s, left 0.3s;
}

#header__nav a:hover::before {
  width: 100%;
  left: 0;
}

.bottom-bar nav a {
  text-decoration: none;
  font-family: var(--font-rubik);
  color: var(--nav-link-color);
  font-size: 20px;
  font-weight: 500;
  transition: color 0.3s;
  padding: 10px 20px;
}

.bottom-bar nav a:hover {
  color: var(--nav-link-hover-color);
}

#bottom_buttons--container {
  display: flex;
  flex-direction: row;
}

#nav__buy-ticket-button,
#nav__book-button {
  background: linear-gradient(145deg, #0d88fa, #0a56a3);
  color: var(--background-color);
  border: none;
  padding: 15px 25px;
  cursor: pointer;
  font-family: var(--font-rubik);
  transition: background-color 0.3s, transform 0.3s;
  margin-right: 80px;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

#nav__buy-ticket-button:hover,
#nav__book-button:hover {
  background-color: var(--button-hover-color);
  transform: scale(1.05);
}

/* Main */

#main-content {
  width: 100% !important;
  margin: 0 auto !important;
  padding-top: 114px;
}

/* Countdown */

#countdown-section {
  width: 100%;
  background: url("../img/background/bgimage8.jpg") no-repeat center center;
  background-size: cover;
  background-color: var(--background-color);
  z-index: 96;
  position: relative;
}

#countdown__container {
  margin: 0 auto;
  width: 600px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  height: 967px;
}

#countdown-header-text {
  font-family: var(--font-press-start);
  font-size: 1.3rem;
  line-height: 1.5;
  color: var(--dark-text-colour);
  margin-bottom: 20px;
  display: block;
  text-align: center;
}

#main-countdown {
  font-family: var(--font-press-start);
  font-size: 1.5rem;
  color: var(--dark-text-colour);
}

.main-content__expo-data {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 40px;
}

#countdown-section p {
  font-size: 18px;
  font-weight: 600;
  cursor: default;
}

#countdown-section a {
  margin: auto;
  text-decoration: none;
  color: var(--nav-link-color);
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
}

.main__buy-ticket-button {
  background: linear-gradient(145deg, #0d88fa, #0a56a3);
  color: var(--background-color);
  border: none;
  padding: 20px 30px;
  cursor: pointer;
  font-family: var(--font-rubik);
  transition: background-color 0.3s, transform 0.3s;
  border-radius: 15px;
  box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.349) !important;
}

.main__buy-ticket-button:hover {
  background-color: var(--button-hover-color);
  transform: scale(1.05);
}

/* About */

#about {
  background: radial-gradient(circle at top left, #4b7394, #000000);
  color: var(--text-colour);
  padding: 50px 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-container {
  max-width: 1200px;
  width: 100%;
  text-align: center;
}

.about-header__container {
  width: 100%;
  color: var(--text-colour);
  text-align: left;
  font-weight: 500;
  border-radius: 35px;
}

.about-header {
  text-align: center;
  font-size: 47px;
  margin-bottom: 45px;
  cursor: default;
}

#about p {
  text-align: center;
  cursor: default;
  line-height: 2.5;
  font-family: var(--font-rubik);
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.05em;
}

/* Program */

#program {
  background: linear-gradient(to bottom, #ffffff, #0074d9);
  color: var(--dark-text-colour);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 20px;
}

#program h1 {
  width: 100%;
  max-width: 1200px;
  font-size: 47px;
  font-weight: 600;
  margin-bottom: 30px;
  text-align: center;
  color: var(--dark-text-colour);
}

.program-list {
  width: 100%;
  max-width: 1200px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.program-list__item {
  background-color: var(--background-color);
  margin: 10px 0;
  padding: 15px 30px;
  border-radius: 10px;
  box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.1);
  font-family: var(--font-rubik);
  font-size: 1.2em;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background-color 0.3s, transform 0.3s;
  color: var(--dark-text-colour);
  cursor: pointer;
  opacity: 0.8 !important;
}

.program-list__item:hover {
  background-color: #f9f9f9 !important;
  transform: translateY(-3px) !important;
}

.program-list__item span {
  font-size: 0.9em;
  font-weight: 400;
  color: var(--nav-link-color);
  margin-left: 10px;
}

/* Lightbox styles */
.lightbox {
  display: none;
  position: fixed;
  z-index: 100;
  left: 0;
  bottom: -100%;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  transition: opacity 2s ease-in-out, bottom 2s ease-in-out;
}

.lightbox.show {
  display: block;
  opacity: 1;
  bottom: 0;
}

.lightbox-content {
  background: linear-gradient(to top, #ffffff, #0074d9);
  margin: 5% auto;
  padding: 0 0 20px 0;
  border: 1px solid #888;
  width: 80%;
  max-width: 600px;
  border-radius: 10px;
  text-align: center;
  transform: scale(0.8);
  transition: transform 2s ease-in-out;
}

.lightbox-content.show {
  transform: scale(1);
}

.lightbox-img {
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.lightbox-logo {
  width: 200px;
  height: auto;
  margin-top: 20px;
}

/* Speakers */

#speakers {
  background: radial-gradient(circle at top left, #4b7394, #000000);
  text-align: center;
  padding: 50px 20px;
  color: var(--text-colour);
  display: flex;
  flex-direction: column;
  align-items: center;
}

#speakers__header {
  font-size: 47px;
  font-weight: 600;
  margin-bottom: 30px;
}

.speakers-grid {
  display: grid;
  gap: 30px;
  width: 100%;
  max-width: 1200px;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
}

.speaker {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  padding: 0;
  background: linear-gradient(to top, #ffffff, #0074d9);
  color: var(--text-colour);
  transition: transform 0.3s, box-shadow 0.3s;
  width: 293px;
  height: 420px;
  border-radius: 30px;
  overflow: hidden;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.speaker img {
  width: 100%;
  height: 65%;
  object-fit: cover;
  margin: 0;
  padding: 0;
}

.speaker-content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 35%;
  width: 100%;
  padding: 10px 0;
}

.speaker p {
  color: var(--dark-text-colour);
  margin: 10px;
  font-size: 1em;
  line-height: 1.5;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.speaker .company-logo {
  width: 100px;
  height: auto;
  object-fit: cover;
}

.speaker.visible:hover {
  transform: scale(1.1);
  box-shadow: 6px 6px 12px rgba(0, 0, 0, 0.3);
}

/* Sponsors */

#sponsors {
  /* height: 800px; */
  text-align: center;
  padding: 50px 20px;
  color: var(--text-colour);
  display: flex;
  flex-direction: column;
  align-items: center;
}

#sponsors__header {
  width: 1200px;
  color: var(--dark-text-colour);
  font-size: 47px;
  font-weight: 600;
  margin-bottom: 30px;
}

.sponsors-grid {
  display: grid;
  gap: 30px;
  width: 100%;
  max-width: 1200px;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
}

.sponsor {
  position: relative;
  text-align: center;
  padding: 20px;
  background-color: #ffffff;
  color: var(--dark-text-colour);
  transition: transform 0.3s;
  width: 200px;
  height: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  perspective: 1000px;
}

.sponsor::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  padding: 4px;
  background: radial-gradient(circle at center, #0074d9, #016199);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Добавление тени от рамки */
}

.sponsor img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.sponsor-inner {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.sponsor:hover .sponsor-inner {
  transform: rotateY(180deg);
}

.sponsor-front,
.sponsor-back {
  padding: 5px;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.sponsor-front {
  background-color: #ffffff;
}

.sponsor-back {
  background: radial-gradient(circle at center, #0074d9, #016199);
  transform: rotateY(180deg);
  color: var(--text-colour);
}

.sponsor-back a {
  color: var(--text-colour);
  text-decoration: none;
  font-weight: bold;
  margin-top: 10px;
}

.sponsor-back a:hover {
  text-decoration: underline;
}

/* Footer */

footer {
  background: radial-gradient(circle at top left, #4b7394, #000000);
  color: #000000;
  padding: 20px;
  text-align: center;
}

footer nav {
  margin-bottom: 10px;
}

footer nav a {
  text-decoration: none;
  font-family: var(--font-rubik);
  color: var(--text-colour);
  font-weight: 700;
  transition: color 0.3s;
  padding: 10px 20px;
}

footer p {
  color: var(--text-colour);
}

/* Sections animation */
.section-animate {
  opacity: 0;
  transform: translateY(100px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.section-animate.visible {
  opacity: 1;
  transform: translateY(0);
}

.program-list__item {
  opacity: 0;
  transform: translateX(-100px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.program-list__item.visible {
  opacity: 1;
  transform: translateX(0);
}

.speaker,
.sponsor {
  opacity: 0;
  transform: scale(0.8);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.speaker.visible,
.sponsor.visible {
  opacity: 1;
  transform: scale(1);
}
