@media (max-width: 1830px) {
  #main-countdown {
    font-size: 2.2rem !important;
  }
}

@media (max-width: 1702px) {
  #nav__book-button,
  #nav__buy-ticket-button {
    font-size: 14px;
  }
  #header__nav a {
    font-size: 18px !important;
  }
}

@media (max-width: 1615px) {
  #nav__book-button,
  #nav__buy-ticket-button {
    font-size: 10px;
    font-weight: bold;
  }
  #header__nav a {
    font-size: 16px !important;
  }
}

@media (max-width: 1512px) {
  #bottom-bar__logo {
    gap: 15px;
  }
  .bottom-bar {
    justify-content: space-evenly;
  }
  .bottom-bar a {
    margin: auto !important;
  }
}

@media (max-width: 1400px) {
  .speakers-grid {
    grid-template-columns: repeat(3, 1fr) !important;
  }
  .sponsors-grid {
    grid-template-columns: repeat(2, 1fr) !important;
  }
}

@media (max-width: 1299px) {
  #header-logo {
    width: 160px !important;
  }
  #bottom-bar__logo a {
    font-size: 12px !important;
  }
}

@media (max-width: 1270px) {
}

@media (max-width: 1249px) {
  #nav-links a {
    padding: 5px 10px !important;
  }
  .program-list {
    padding: 0 20px !important;
    box-sizing: border-box;
  }
}

@media (max-width: 1130px) {
  #bottom_buttons--container {
    display: none !important;
  }
  #nav-links {
    display: none;
  }

  .mobile-nav-btn {
    display: block !important;
    position: absolute !important;
    top: 35px;
    right: 35px;
  }
}

@media (max-width: 1125px) {
  .program-list__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 10px;
  }

  .program-time,
  .program-topic,
  .program-speaker__name {
    width: 100%;
  }
}

@media (max-width: 1024px) {
  .speakers-grid {
    grid-template-columns: repeat(2, 1fr) !important;
  }
}

@media (max-width: 768px) {
  body,
  html {
    margin: 0 !important;
    padding: 0 !important;
    overflow-x: hidden !important;
    font-size: 14px !important;
  }

  /* Intro screen */
  #intro-screen {
    padding-top: 10px !important;
    padding-bottom: 10px !important;
  }

  #bottom-bar__logo {
    margin-top: 12px;
  }

  #logo {
    width: 300px !important;
    margin-bottom: 20px !important;
  }

  #countdown-intro__text {
    font-size: 18px !important;
    margin-bottom: 10px !important;
  }

  #intro__header {
    font-size: 24px !important;
    margin: 10px 0 !important;
  }

  #intro-countdown {
    width: 100% !important;
    font-size: 1.5em !important;
    margin-bottom: 20px !important;
  }

  #get-button {
    padding: 10px 20px !important;
    font-size: 1em !important;
    margin-top: 15px !important;
  }

  /* Header */
  .top-bar {
    gap: 40px !important;
    padding: 10px !important;
  }

  .bottom-bar {
    flex-direction: column !important;
    align-items: flex-start !important;
    padding: 10px !important;
  }

  #header__nav a {
    font-size: 16px !important;
    padding: 5px 10px !important;
  }

  #nav__book-button,
  #nav__buy-ticket-button {
    font-size: 12px !important;
    padding: 10px 15px !important;
    margin-right: 10px !important;
  }

  /* Main content */
  #main-content {
    padding-top: 90px !important;
  }

  #countdown__container {
    width: 100% !important;
    padding: 0 10px !important;
    box-sizing: border-box !important;
  }

  #countdown-header-text {
    font-size: 1rem !important;
    margin-bottom: 10px !important;
  }

  #main-countdown {
    font-size: 1.2rem !important;
  }

  .main-content__expo-data p,
  .main-content__expo-data a {
    font-size: 14px !important;
  }

  .main__buy-ticket-button {
    padding: 10px 15px !important;
    font-size: 14px;
  }

  /* About section */
  #about {
    padding: 30px 10px !important;
  }

  .about-header {
    font-size: 30px !important;
    margin-bottom: 30px !important;
  }

  #about p {
    font-size: 14px !important;
    line-height: 1.8 !important;
  }

  /* Program section */
  #program {
    padding: 30px 10px !important;
  }

  #program h1 {
    font-size: 30px !important;
    margin-bottom: 20px !important;
  }

  .program-list__item {
    padding: 10px 15px !important;
    font-size: 14px !important;
  }

  .program-list__item span {
    font-size: 0.8em !important;
  }

  /* Lightbox styles */
  .lightbox-content {
    width: 90% !important;
    max-width: 500px !important;
  }

  /* Speakers section */
  #speakers {
    padding: 30px 10px !important;
  }

  #speakers__header {
    font-size: 30px !important;
    margin-bottom: 20px !important;
  }

  .speaker {
    /* width: 100% !important; */
    height: auto !important;
  }

  .speaker.visible:hover {
    transform: none !important;
  }

  /* Sponsors section */
  #sponsors {
    padding: 30px 10px !important;
  }

  #sponsors__header {
    font-size: 30px !important;
    margin-bottom: 20px !important;
  }

  /* Footer */
  footer {
    padding: 10px !important;
  }

  footer nav {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
  }

  footer nav a {
    font-size: 14px !important;
    padding: 5px !important;
  }

  footer p {
    font-size: 12px !important;
  }

  /* Section animations */
  .section-animate {
    opacity: 1 !important;
    transform: translateY(0) !important;
  }
}

@media (max-width: 620px) {
  .speakers-grid {
    grid-template-columns: repeat(1, 1fr) !important;
    gap: 20px !important;
  }
  .sponsors-grid {
    grid-template-columns: repeat(1, 1fr) !important;
    gap: 20px !important;
  }
}

@media (max-width: 475px) {
  #event-location {
    display: none !important;
  }
  #bottom-bar__logo a {
    display: none !important;
  }
  .bottom-bar {
    display: flex !important;
    flex-direction: row !important;
    text-align: center !important;
    justify-content: center !important;
  }
  #bottom-bar__logo {
    margin-top: 12px;
  }
  #header-logo {
    margin-left: 0 !important;
  }
  .main-content__expo-data {
    flex-direction: column !important;
    gap: 20px !important;
  }

  .mobile-nav-btn {
    top: 40px;
    right: 5px;
  }
}

@media (max-width: 412px) {
  .bottom-bar {
    justify-content: start !important;
  }
}
