.no-scroll {
  overflow: hidden;
}

.mobile-nav {
  position: fixed;
  top: 0;
  right: -375px;
  width: 375px;
  height: 100%;
  z-index: 999;

  display: flex;
  flex-direction: column;

  align-items: flex-start;
  padding: 40px;

  background: linear-gradient(209deg, #4287b3 -17.38%, #133f6b 78.4%), #276195;
  background-color: #141d22;
  transition: all 0.4s ease-in;
}

.mobile-nav--open {
  right: 0;
}

.nav__user--mobile-nav {
  margin: 0 auto;
  color: #fff;
  padding: 20px 0 0 0;
}

.mobile-nav-fade {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #141d22;
  opacity: 0;
  z-index: 99;
  pointer-events: none;
}

.mobile-nav__list {
  display: grid;
  gap: 45px;
  text-transform: capitalize;
  color: #fdfdfdd5;
}

.mobile-nav__list a {
  color: #fff !important;
}

.mobile-nav__list a:hover {
  color: #ff4444;
}

.mobile-nav-fade--open {
  opacity: 0.7 !important;
  pointer-events: all !important;
}

#mobile-nav__book-button,
#mobile-nav__buy-ticket-button {
  margin: 15px;
  background: linear-gradient(145deg, #0d88fa, #0a56a3);
  color: var(--background-color);
  border: none;
  padding: 15px 25px;
  cursor: pointer;
  font-family: var(--font-rubik);
  transition: background-color 0.3s, transform 0.3s;
  margin-right: 80px;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
